<template>
    <div id="app">


        <table cellpadding="0" cellspacing="1" border="1" class="smart-table" width="100%">


            <tr>
                <td align="right">订单编号:</td>
                <td>

                    {{shouhou.orderno}}

                </td>
            </tr>


            <tr>
                <td align="right">商品名:</td>
                <td>

                    {{shouhou.spname}}

                </td>
            </tr>



            <tr>
                <td align="right">数量:</td>
                <td>

                    {{shouhou.count}}

                </td>
            </tr>


            <tr>
                <td align="right">价格(元):</td>
                <td>

                    {{shouhou.price}} ¥

                </td>
            </tr>


            <tr>
                <td align="right">总价(元):</td>
                <td>

                    {{shouhou.totalfee}}

                </td>
            </tr>


            <tr>
                <td align="right">图片:</td>
                <td>

                    <img width="120" height="120" :src="hostHead+shouhou.tupian"/>


                </td>
            </tr>


            <tr>
                <td align="right">状态:</td>
                <td>

                    <input type="radio" value="2" v-model="state" checked="checked"/>同意
                    <input type="radio" value="3" v-model="state"/>拒绝

                </td>
            </tr>


            <tr>
                <td align="right">创建时间:</td>
                <td>
                    {{shouhou.createtime}}
                </td>
            </tr>


            <tr>
                <td align="right">原因:</td>
                <td>

                    {{shouhou.reason}}

                </td>
            </tr>


            <tr>
                <td align="right">退货描述:</td>
                <td>
                    <div v-html="shouhou.tdes"></div>

                </td>
            </tr>




            <tr>
                <td align="right">处理说明:</td>
                <td colspan="3">
                    <textarea name="reply" v-model="replydes" style="width:48%;height:80px;"></textarea>
                </td>
            </tr>

            <tr>
                <td colspan="4">
                    <button @click="submitCheck" class="btn btn-danger"><i class="fa fa-plus"></i>提交</button>
                </td>
            </tr>


        </table>

    </div>
</template>

<script>
    import {VueUtil} from "../util/vueutil";

    export default {
        name: "shouhouspnext",
        data() {
            return {
                "state": "2",
                replydes: "",
                hostHead: this.$app.contextPath,
                "shouhou": {},
            }
        },
        props:{
          params:{
              type:Object
          }
        },
        methods: {
            //begin load
            async load() {
                let id =this.params.id;

                if (id != null) {
                    let url = "admin/shouhou/load";
                    let {data: res} = await this.$http.post(url, {id: id});
                    console.log("res", res);
                    if (res != null && res.data != null)
                        this.shouhou = res.data;
                }
            },//end load
            async submitCheck() {
                let id = this.params.id;
                let util = new VueUtil(this);
                if (id != null) {
                    let url = "admin/shouhou/shenpi";
                    let {data: res} = await util.http.post(url, {
                        id: id,
                        "state": this.state,
                        replydes: this.replydes
                    });
                    console.log("res", res);
                    if (res != null && res.stateCode < 0) {
                        this.$message.error(res.des);
                        return;
                    }
                }
                this.$emit("close",false);
            }

        },
        created() {
            this.load();
        }
    }
</script>

<style scoped>

</style>
